function ProductCard({ product }: { product: Product }) {
  const [quantity, setQuantity] = useState(0)
  const [isHovered, setIsHovered] = useState(false)

  const handleAddToCart = () => {
    setQuantity(1)
  }

  const handleIncrement = () => {
    setQuantity(prev => prev + 1)
  }

  const handleDecrement = () => {
    setQuantity(prev => (prev > 1 ? prev - 1 : 0))
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      viewport={{ once: true }}
      transition={{ duration: 0.5 }}
      className="group bg-white rounded-2xl border border-border overflow-hidden hover:shadow-xl transition-all duration-300"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/* Image Container */}
      <div className="relative aspect-square overflow-hidden bg-muted">
        <Image
          src={product.image}
          alt={product.name}
          fill
          className="object-cover transition-transform duration-500 group-hover:scale-110"
        />
        
        {/* Badges */}
        <div className="absolute top-3 left-3 flex flex-col gap-2">
          {product.isNew && (
            <Badge className="bg-[#22c55e] hover:bg-[#22c55e] text-white border-0">
              Новинка
            </Badge>
          )}
          {product.isBestseller && (
            <Badge className="bg-[#16a34a] hover:bg-[#16a34a] text-white border-0">
              Хит продаж
            </Badge>
          )}
        </div>

        {/* Hover Overlay */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: isHovered ? 1 : 0 }}
          className="absolute inset-0 bg-black/40 flex items-center justify-center"
        >
          <div className="bg-white/95 backdrop-blur-sm rounded-xl p-4 text-center max-w-[200px]">
            <Leaf className="w-6 h-6 text-[#16a34a] mx-auto mb-2" />
            <p className="text-xs text-muted-foreground">{product.description}</p>
          </div>
        </motion.div>
      </div>

      {/* Content */}
      <div className="p-5">
        <div className="flex items-start justify-between mb-2">
          <h3 className="font-semibold text-foreground text-lg leading-tight">
            {product.name}
          </h3>
          <span className="text-xs text-muted-foreground bg-muted px-2 py-1 rounded-full">
            {product.weight}
          </span>
        </div>

        <div className="flex items-center justify-between mt-4">
          <div>
            <span className="text-2xl font-bold text-[#16a34a]">
              {product.price} ₽
            </span>
          </div>

          {quantity === 0 ? (
            <Button
              onClick={handleAddToCart}
              className="bg-[#16a34a] hover:bg-[#15803d] text-white shadow-md hover:shadow-lg transition-all"
            >
              <ShoppingCart className="w-4 h-4 mr-2" />
              В корзину
            </Button>
          ) : (
            <div className="flex items-center gap-2 bg-[#dcfce7] rounded-lg p-1">
              <Button
                variant="ghost"
                size="icon"
                onClick={handleDecrement}
                className="h-8 w-8 hover:bg-[#16a34a] hover:text-white"
              >
                <Minus className="w-4 h-4" />
              </Button>
              <span className="w-8 text-center font-semibold text-[#16a34a]">
                {quantity}
              </span>
              <Button
                variant="ghost"
                size="icon"
                onClick={handleIncrement}
                className="h-8 w-8 hover:bg-[#16a34a] hover:text-white"
              >
                <Plus className="w-4 h-4" />
              </Button>
            </div>
          )}
        </div>
      </div>
    </motion.div>
  )
}

